import styled, { css } from "styled-components";
import { gradientBG } from "./../theme/animation";

export const Background = styled.div`
  background: linear-gradient(
    to right bottom,
    #3c3b3f,
    #473f46,
    #554247,
    #614744,
    #664e3e,
    #60533a,
    #56583b,
    #495d42,
    #395c4b,
    #2b5b56,
    #26575f,
    #2c5364
  );
  height: 100vh;
  width: 100%;
  background-size: 400% 400%;
  background-attachment: fixed;
  ${(props) =>
    props.start &&
    css`
      animation: ${gradientBG} 10s ease infinite;
    `}
`;

export const Navigation = styled.nav`
  color: #eee;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 1.2rem;
  margin-left: 5rem;
  margin-right: 5rem;
  /* position: sticky;
	top: 0; */

  @media (max-width: 480px) {
    align-items: flex-end;
    padding: 1rem 0 0 1rem;
    margin: 0;
  }
`;

export const Logo = styled.div`
  width: 12.2rem;

  @media (max-width: 480px) {
    width: 6rem;
  }
`;

export const Img = styled.img`
  width: 100%;
`;

export const NavigationList = styled.ul`
  list-style: none;
  font-size: 1.2rem;
  font-weight: 400;
  @media (max-width: 480px) {
    display: none;
  }
`;

export const NavigationListLinks = styled.p`
  display: inline;
  margin: 1.2rem;
  background-color: transparent;
  border: none;
  position: relative;
  padding: 6px;
  cursor: pointer;

  ${(props) =>
    props.ms &&
    css`
      color: #eee;
    `}

  &:hover::after,
	&:hover::before {
    width: 100%;
  }

  &::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    height: 2px;
    width: 0%;
    background-color: #29d256;
    transition: all 0.5s;
  }

  &::before {
    position: absolute;
    content: "";
    bottom: 0;
    right: 0;
    height: 2px;
    width: 0%;
    background-color: #29d256;
    transition: all 0.5s;
  }
`;

export const HeroWrapper = styled.div`
  width: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

export const HeroTextBoxSay = styled.h2`
  color: #29d256;
  text-transform: uppercase;
  font-size: 0.9rem;
  letter-spacing: 0.5rem;
  text-align: center;
  line-height: 2rem;
  margin-bottom: 1rem;

  @media (max-width: 480px) {
    font-size: 0.8rem;
    letter-spacing: 0.3rem;
    line-height: 1.5rem;
    margin-bottom: 0.8rem;
  }
`;

export const HeroText = styled.h1`
  color: #eee;
  font-family: "Adinda Melia";
  font-weight: 800;
  font-size: 4rem;
  letter-spacing: 0.5rem;
  text-align: center;
  margin-bottom: 1rem;

  @media (max-width: 480px) {
    font-size: 2.3rem;
  }
`;

export const HeroTextBox = styled.h3`
  color: #eee;
  font-weight: 200;
  /*font-size: 1rem;
	letter-spacing: 0.2rem; */
  text-align: center;
  line-height: 2rem;
  margin-top: 2rem;

  @media (max-width: 480px) {
    margin-top: 1.5rem;
  }
`;

export const HeroTextBoxHead = styled.span`
  font-size: 2rem;
  line-height: 2rem;

  @media (max-width: 480px) {
    font-size: 1.5rem;
    line-height: 1.5rem;
  }
`;

export const HeroTextDesign = styled.span`
  font-size: 2rem;
  text-decoration: underline;
  color: #29d256;

  @media (max-width: 480px) {
    font-size: 1.5rem;
    line-height: 1.5rem;
  }
`;

export const HamBurger = styled.div`
  padding-top: 1.2rem;
  padding-right: 1.2rem;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 55;
`;

export const HamBurgerBar = styled.div`
  margin-bottom: 5px;
  width: 25px;
  border: 1px solid #eee;
`;

export const HamBurgerMenu = styled.div`
  position: fixed;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  width: 100vw;
  background-color: #333c3d;
  background-size: cover;
  font-size: 1.5rem;
  z-index: 20;
`;
