import React from "react";
import { ListContainer, Time, Item, Description, LogoImg } from "./Style";

const ExperienceList = (props) => {
  return (
    <>
      {props.list &&
        props.list.map((el, i) => {
          return (
            <ListContainer key={i + 1}>
              <div>
                <LogoImg src={el.icon} alt={el.company} />
              </div>
              <div>
                <Time>{el.time}</Time>
                <Item>{el.position}</Item>
                <Item>{el.company}</Item>
                <Description>{el.description}</Description>
              </div>
            </ListContainer>
          );
        })}
    </>
  );
};

export default ExperienceList;
