import styled from "styled-components";

export const ListContainer = styled.div`
  display: flex;
  align-items: center;
  margin-left: 4rem;
  padding-bottom: 1.5rem;
  padding-top: 1.5rem;
  &:first-child {
    padding-top: 3rem;
  }
  border-bottom: 1px solid #495556;

  @media (max-width: 480px) {
    margin: 0;
    padding-bottom: 1.5rem;
  }
`;

export const Icon = styled.div`
  color: #29d256;
  padding-right: 1.5rem;
  font-size: 4rem;

  @media (max-width: 480px) {
    font-size: 2.5rem;
  }
`;

export const Time = styled.p`
  color: #29d256;
  padding: 0.2rem;
  font-size: 1.3rem;

  @media (max-width: 480px) {
    font-size: 1rem;
  }
`;

export const Item = styled.p`
  color: #eee;
  padding: 0.3rem;
  font-weight: bold;
  font-size: 1.4rem;

  @media (max-width: 480px) {
    font-size: 1.2rem;
  }
`;

export const Description = styled.p`
  color: #9c9999;
  padding: 0.3rem;
  font-size: 1.3rem;

  @media (max-width: 480px) {
    font-size: 1rem;
  }
`;

export const LogoImg = styled.img`
  height: 5rem;
  width: 5rem;
  margin-right: 1rem;
`;
