import React from "react";
import { ExperienceContainer, Header } from "./Style";
import ExperienceList from "./components/ExperienceList";
import naaniz_logo from "./../../../staticFiles/company_logo/naaniz_logo.png";
import cv_logo from "./../../../staticFiles/company_logo/cv_logo.png";
import oetd_logo from "./../../../staticFiles/company_logo/oetd_logo.png";
import st_logo from "./../../../staticFiles/company_logo/st_logo.png";

const Experience = () => {
  const list = [
    {
      time: "August 2020 - February 2021",
      position: "MERN Stack Developer",
      company: "Simplyturn Technologies Pvt Ltd",
      description:
        "Worked on HR Management Software, Develop all type of CURD RESTFull APIs like chat, profile, auth and Other CURD with complex logic.",
      icon: st_logo,
    },
    {
      time: "August 2020 - October 2020",
      position: "Node.js Developer",
      company: "OETD Pvt Ltd",
      description:
        "Develop Blockchain-based APIs and Handle complex backend Logic with Node.js",
      icon: cv_logo,
    },

    {
      time: "July 2020 - September 2020",
      position: "Node.js Developer",
      company: "Code Variac",
      description: "Develop APIs and backend logic for Apps.",
      icon: oetd_logo,
    },
    {
      time: "June 2020 - August 2020",
      position: "MERN Team Lead",
      company: "Naaniz",
      description: "Lead Frontend and Backend team.",
      icon: naaniz_logo,
    },
  ];

  return (
    <ExperienceContainer name="section-Resume-Experience">
      <Header data-aos="fade-left">Experience</Header>
      <div data-aos="fade-left">
        <ExperienceList list={list} />
      </div>
    </ExperienceContainer>
  );
};

export default Experience;
